<template>
  <div class="view-container">

    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!--  -->
    <div class="row ope-container">
      <el-button type="primary" icon="el-icon-refresh" size="small" @click="getRoleData()"></el-button>
      <el-button type="success" size="small" @click="addRole()"></el-button>
      <Search v-on:search="getRoleData()"></Search>
    </div>
    <!--  -->
    <div class="row">
      <el-table :data="roleData" border style="width: 100%" :max-height="tabMaxHeight">
        <el-table-column label="ID" prop="id">
        </el-table-column>
        <el-table-column label="" prop="name">
        </el-table-column>
        <el-table-column label=" " prop="createTime">
        </el-table-column>
        <el-table-column label=" " prop="updateTime">
        </el-table-column>
        <el-table-column label=" " width="300">
          <template slot-scope="scope ">
            <el-button type="danger " size="mini " @click="delRoleItem(scope.row) ">
              <i class="el-icon-delete "></i>
            </el-button>
            <el-button type="primary " size="mini " @click="editRoleItem(scope.row) ">
              <i class="el-icon-edit "></i>
            </el-button>
            <el-button type="primary " size="mini " @click="addRolePermission(scope.row) ">
            </el-button>
            <el-button type="primary " size="mini " @click="addRoleMenu(scope.row) ">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="row page-container ">
      <el-pagination @size-change="handleSizeChange " @current-change="handleCurrentChange " :current-page="currentPage " :page-sizes="[10, 20] " :page-size="pageSize " layout="total, sizes, prev, pager, next, jumper " :total="totalSize ">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="addRoleDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="120px" :model="addRoleData">
        <el-form-item label="" prop="code">
          <el-input v-model="addRoleData.code "></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="addRoleData.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addRoleDialogVisible = false"> </el-button>
        <el-button type="primary" size="small " @click="submitAddRole('addRoleData') "> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title=" " :visible.sync="delRoleDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer ">
        <el-button size="small" @click="delRoleDialogVisible = false"> </el-button>
        <el-button type="danger" size="small" @click="submitDelRole()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editRoleDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="120px " :model="editRoleData">
        <el-form-item label="" prop="code">
          <el-input v-model="editRoleData.code"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editRoleData.name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editRoleDialogVisible = false"> </el-button>
        <el-button type="primary" size="small " @click="submitEditRole( 'editRoleData') "> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" class="edit-role-permission" :visible.sync="editRolePermissionDialogVisible" width="30%" :before-close="handleClose" ref="multipleTable">
      <el-select v-model="rolePermissionData" multiple placeholder="">
        <el-option v-for="item in permissionData" :key="item.id" :label="item.name" :value="item.id">
        </el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editRolePermissionDialogVisible = false"> </el-button>
        <el-button type="primary" size="small " @click="submitEditRolePermission() "> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" class="edit-role-permission" :visible.sync="editRoleMenuDialogVisible" width="30%" :before-close="handleClose" ref="multipleTable">
      <el-tree node-key="id" default-expand-all :expand-on-click-node="false" :data="menuList" :props="defaultProps" show-checkbox :default-checked-keys="roleMenuData" ref="tree">
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editRoleMenuDialogVisible = false"> </el-button>
        <el-button type="primary" size="small " @click="submitEditRoleMenu() "> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import Search from './Commen/Search.vue'
import roleService from '../../service/roleService.js'
import permissionService from '../../service/permissionService.js'
import menuService from '../../service/menuService.js'
export default {
  name: 'Role',
  data() {
    return {
      // 
      addRoleDialogVisible: false,
      // 
      addRoleData: {
        code: '',
        name: ''
      },
      //   
      editRoleDialogVisible: false,
      //   
      editRoleData: {
        id: '',
        code: '',
        name: ''
      },
      //   
      delRoleDialogVisible: false,
      //id
      delRoleId: '',
      //
      currentPage: 1,
      pageSize: 20,
      totalSize: 0,
      // 
      roleData: [],
      // 
      tabMaxHeight: 0,
      // Id
      currentRoleId: '',

      // 
      editRolePermissionDialogVisible: false,
      // 
      permissionData: [],
      // 
      rolePermissionData: [],

      //
      editRoleMenuDialogVisible: false,
      // 
      menuList: [],
      // 
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      // 
      roleMenuData: [],
      rolePath: {
        getPath: '/role/page',
        addPath: '/role/add',
        delPath: '/role/delete',
        editPath: '/role/update',
        searchPermisssionPath: '/role/authorities',
        addPermisssionPath: '/role/config/authorities',
        searchMenuPath: '/role/menu',
        addMenuPath: '/role/config/menu'
      },
      permissionPath: {
        getPath: '/authority/page'
      },
      menuPath: {
        getPath: '/menu/all'
      }
    }
  },
  computed: {
    searchId() {
      return this.$store.state.user.searchId
    }
  },
  components: {
    Search
  },
  mounted() {
    // 
    this.getRoleData()
  },
  methods: {
    //  
    getRoleData() {
      roleService
        .getRoleData(this.rolePath.getPath, {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          name: this.searchId
        })
        .then(res => {
          let result = res.data
          this.roleData = result.data.list
          this.totalSize = result.data.total
          let clientHieght = document.body.clientHeight
          this.tabMaxHeight = clientHieght - 60 - 30 - 30 - 50 - 50
        })
    },
    //  
    addRole() {
      this.addRoleData = {
        code: '',
        name: ''
      }
      this.addRoleDialogVisible = true
    },
    // 
    submitAddRole() {
      this.addRoleDialogVisible = false
      roleService.addRoleData(this.rolePath.addPath, this.addRoleData).then(res => {
        this.getRoleData()
      })
    },
    // 
    delRoleItem(row) {
      this.delRoleDialogVisible = true
      this.delRoleId = row.id
    },
    // 
    submitDelRole() {
      this.delRoleDialogVisible = false
      roleService
        .delRoleData(this.rolePath.delPath, {
          id: this.delRoleId
        })
        .then(res => {
          this.getRoleData()
        })
    },
    // 
    editRoleItem(row) {
      this.editRoleData.id = row.id
      this.editRoleData.code = row.code
      this.editRoleData.name = row.name
      this.editRoleDialogVisible = true
    },
    // 
    submitEditRole() {
      this.editRoleDialogVisible = false
      roleService.updateRoleData(this.rolePath.editPath, this.editRoleData).then(res => {
        this.getRoleData()
      })
    },
    // 
    addRolePermission(row) {
      this.currentRoleId = row.id
      this.editRolePermissionDialogVisible = true
      // 
      permissionService.getPermissionData(this.permissionPath.getPath, {}).then(res => {
        this.permissionData = res.data.data.list
      })
      // 
      roleService
        .searchRolePermission(this.rolePath.searchPermisssionPath, {
          roleId: this.currentRoleId
        })
        .then(res => {
          this.rolePermissionData = res.data.data
        })
    },
    // 
    submitEditRolePermission() {
      this.editRolePermissionDialogVisible = false
      roleService
        .addRolePermission(this.rolePath.addPermisssionPath, {
          roleId: this.currentRoleId,
          authorityIds: this.rolePermissionData
        })
        .then(res => {
          this.getRoleData()
        })
    },
    // 
    addRoleMenu(row) {
      this.currentRoleId = row.id
      this.editRoleMenuDialogVisible = true
      // 
      menuService.getMenuList(this.menuPath.getPath, {}).then(res => {
        this.menuList = res.data.data
      })
      // 
      roleService
        .searchRoleMenu(this.rolePath.searchMenuPath, {
          roleId: this.currentRoleId
        })
        .then(res => {
          this.roleMenuData = res.data.data
        })
    },
    // 
    submitEditRoleMenu() {
      this.editRoleMenuDialogVisible = false
      roleService
        .addRoleMenu(this.rolePath.addMenuPath, {
          roleId: this.currentRoleId,
          menuIds: this.$refs.tree.getCheckedKeys()
        })
        .then(res => {
          this.getRoleData()
        })
    },
    // 
    handleClose() {
      this.addRoleDialogVisible = false
      this.delRoleDialogVisible = false
      this.editRoleDialogVisible = false
      this.editRolePermissionDialogVisible = false
      this.editRoleMenuDialogVisible = false
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.pageSize = val
      this.getRoleData()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getRoleData()
    }
  }
}
</script>

<style>
.view-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
}
.breadcrumb-container {
  height: 20px;
}
.ope-container {
  height: 40px;
}
.data-container {
  flex: 1;
  overflow: auto;
  display: flex;
}
.page-container {
  height: 40px;
}
</style>




