<template>
    <div class="view-container">
        <!---->
        <div class="row breadcrumb-container">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <!--  -->
        <div class="row ope-container">
            <el-button type="success" size="small" @click="addCategory()"> </el-button>
        </div>

        <!--  -->
        <div class="row data-container">
            <el-table :data="categoryDate" border style="width: 100%" :max-height="tabMaxHeight">
                <el-table-column label="ID" prop="">
                </el-table-column>

                <el-table-column label="" prop="">
                </el-table-column>
                <el-table-column label="" prop="">
                </el-table-column>
                <el-table-column label="" prop="">
                </el-table-column>
                <el-table-column label="">
                    <template slot-scope="scope">
                        <el-button type="danger" size="mini" @click="delCategoryItem(scope.row)">
                            <i class="el-icon-delete"></i>
                        </el-button>
                        <el-button type="primary" size="mini" @click="editCategoryItem(scope.row)">
                            <i class="el-icon-edit"></i>
                        </el-button>
                    </template>
                </el-table-column>

            </el-table>
        </div>

        <!--  -->
        <el-dialog title="" :visible.sync="addCategoryDialogVisible" width="30%" :before-close="handleClose">
            <el-input v-model="addCategoryDate.name" placeholder=""></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="addCategoryDialogVisible = false"> </el-button>
                <el-button type="primary" size="small" @click="submitAddCategory()"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" :visible.sync="editCategoryDialogVisible" width="30%" :before-close="handleClose">
            <el-input v-model="editCategoryDate.name" placeholder=""></el-input>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="editCategoryDialogVisible = false"> </el-button>
                <el-button type="primary" size="small" @click="submitEditCategory()"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog title="" class="add-video" :visible.sync="delCategoryDialogVisible" width="30%" :before-close="handleClose">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="delCategoryDialogVisible = false"> </el-button>
                <el-button type="danger" size="small" @click="submitDelCategory()"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
  name: 'video-list',
  data() {
    return {
      // 
      categoryDate: [''],
      tabMaxHeight: 300,

      //   
      addCategoryDialogVisible: false,
      //   
      addCategoryDate: {
        name: ''
      },
      //   
      editCategoryDialogVisible: false,
      //   
      editCategoryDate: {
        name: ''
      },
      //
      delCategoryDialogVisible: false
    }
  },
  methods: {
    //   
    addCategory() {
      this.addCategoryDialogVisible = true
    },
    // 
    submitAddCategory() {},
    //
    delCategoryItem() {
      this.delCategoryDialogVisible = true
    },
    // 
    submitDelCategory() {},
    // 
    editCategoryItem() {
      this.editCategoryDialogVisible = true
    },
    // 
    submitEditCategory() {},
    // 
    handleClose() {
      this.addCategoryDialogVisible = false
      this.editCategoryDialogVisible = false
      this.delCategoryDialogVisible = false
    }
  }
}
</script>

<style>
.add-video .el-select {
  width: 100%;
}
</style>




