<template>
  <div class="view-container">

    <!---->
    <div class="row breadcrumb-container">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <!--  -->
    <div class="row ope-container">
      <el-button type="primary" icon="el-icon-refresh" size="small" @click="getAdminData()"></el-button>
      <el-button type="success" size="small" @click="addAdmin()"></el-button>
      <Search v-on:search="getAdminData()"></Search>
    </div>

    <!--  -->
    <div class="row data-container">
      <el-table :data="adminData" border style="width: 100%" :max-height="tabMaxHeight">
        <el-table-column label="ID" prop="id">
        </el-table-column>
        <el-table-column label="" prop="username">
        </el-table-column>
        <el-table-column label="" prop="realname">
        </el-table-column>
        <el-table-column label="" prop="email">
        </el-table-column>
        <el-table-column label="" prop="createTime">
          <template slot-scope="scope">
            <div>{{moment(scope.row.createTime)}}</div>
          </template>
        </el-table-column>
        <el-table-column label="" prop="updateTime">
          <template slot-scope="scope">
            <div>{{moment(scope.row.updateTime)}}</div>
          </template>
        </el-table-column>
        <el-table-column label="" width="350">
          <template slot-scope="scope">
            <el-button type="danger" size="mini" @click="delAdminItem(scope.row)">
              <i class="el-icon-delete"></i>
            </el-button>
            <el-button type="primary" size="mini" @click="editAdminItem(scope.row)">
              <i class="el-icon-edit"></i>
            </el-button>
            <el-button type="primary " size="mini " @click="addAdminPermission(scope.row) ">
            </el-button>
            <el-button type="primary " size="mini " @click="addAdminMenu(scope.row) ">
            </el-button>
            <el-button type="primary " size="mini " @click="addAdminRole(scope.row) ">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!--  -->
    <div class="row page-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 20]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalSize">
      </el-pagination>
    </div>

    <!--  -->
    <el-dialog title="" :visible.sync="addAdminDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="120px" :model="addAdminData">
        <el-form-item label="" prop="username">
          <el-input v-model="addAdminData.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addAdminData.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="realname">
          <el-input v-model="addAdminData.realname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addAdminData.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="addAdminData.telephone"></el-input>
        </el-form-item>

      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addAdminDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitAddAdmin('addAdminData')"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="delAdminDialogVisible" width="30%" :before-close="handleClose">
      <span></span>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="delAdminDialogVisible = false"> </el-button>
        <el-button type="danger" size="small" @click="submitDelAdmin()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editAdminDialogVisible" width="30%" :before-close="handleClose">
      <el-form label-position="left" label-width="120px" :model="editAdminData">
        <el-form-item label="" prop="password">
          <el-input v-model="editAdminData.password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="realname">
          <el-input v-model="editAdminData.realname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editAdminData.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="editAdminData.telephone"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editAdminDialogVisible = false"> </el-button>
        <el-button type="primary" size="small" @click="submitEditAdmin('editAdminData')"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editAdminPermissionDialogVisible" width="30%" :before-close="handleClose" ref="multipleTable">
      <el-select v-model="adminPermissionData" multiple placeholder="">
        <el-option v-for="item in permissionData" :key="item.id" :label="item.name" :value="item.id">
        </el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editAdminPermissionDialogVisible = false"> </el-button>
        <el-button type="primary" size="small " @click="submitEditAdminPermission() "> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editAdminRoleDialogVisible" width="30%" :before-close="handleClose" ref="multipleTable">
      <el-select v-model="adminRoleData" multiple placeholder="">
        <el-option v-for="item in roleData" :key="item.id" :label="item.name" :value="item.id">
        </el-option>
      </el-select>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editAdminRoleDialogVisible = false"> </el-button>
        <el-button type="primary" size="small " @click="submitEditAdminRole() "> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="editAdminMenuDialogVisible" width="30%" :before-close="handleClose" ref="multipleTable">
      <el-tree node-key="id" default-expand-all :expand-on-click-node="false" :data="menuList" :props="defaultProps" show-checkbox :default-checked-keys="adminMenuData" ref="tree">
      </el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="editAdminMenuDialogVisible = false"> </el-button>
        <el-button type="primary" size="small " @click="submitEditAdminMenu() "> </el-button>
      </span>
    </el-dialog>

  </div>
</template>
<script>
import Search from './Commen/Search.vue'
import moment from 'moment'
import adminService from '../../service/adminService.js'
import permissionService from '../../service/permissionService.js'
import roleService from '../../service/roleService.js'
import menuService from '../../service/menuService.js'
export default {
  name: 'Admin',
  data() {
    return {
      // 
      addAdminDialogVisible: false,
      // 
      addAdminData: {
        username: '',
        password: '',
        realname: '',
        email: '',
        telephone: '',
        isEnable: false
      },
      //   
      editAdminDialogVisible: false,
      // 
      editAdminData: {
        id: '',
        password: '',
        realname: '',
        email: '',
        telephone: '',
        isEnable: false
      },
      //   
      delAdminDialogVisible: false,
      // id
      delAdminId: 0,
      // 
      adminData: [],
      //
      currentPage: 1,
      pageSize: 20,
      totalSize: 0,
      // 
      tabMaxHeight: 0,

      // Id
      currentAdminId: '',
      // 
      editAdminPermissionDialogVisible: false,
      // 
      permissionData: [],
      // 
      adminPermissionData: [],

      // 
      editAdminRoleDialogVisible: false,
      // 
      roleData: [],
      // 
      adminRoleData: [],

      // 
      editAdminMenuDialogVisible: false,
      // 
      menuList: [],
      // 
      defaultProps: {
        children: 'children',
        label: 'name'
      },
      // 
      adminMenuData: [],

      adminPath: {
        getPath: '/bguser/page',
        addPath: '/bguser/add',
        delPath: '/bguser/delete',
        editPath: '/bguser/update',
        searchPermissionPath: '/bguser/privilege/authorities',
        addPermissionPath: '/bguser/config/privilege/authorities',
        searchRolePath: '/bguser/roles',
        addRolePath: '/bguser/config/roles',
        searchMenuPath: '/bguser/privilege/menu',
        addMenuPath: '/bguser/config/privilege/menu'
      },
      permissionPath: {
        getPath: '/authority/page'
      },
      rolePath: {
        getPath: '/role/page'
      },
      menuPath: {
        getPath: '/menu/all'
      }
    }
  },
  computed: {
    searchId() {
      return this.$store.state.user.searchId
    }
  },
  components: {
    Search
  },
  mounted() {
    this.getAdminData()
  },
  methods: {
    // 
    moment(time) {
      return moment(time).format('YYYY-MM-DD')
    },
    //  
    getAdminData() {
      adminService
        .getAdminData(this.adminPath.getPath, {
          pageNum: this.currentPage,
          pageSize: this.pageSize,
          name: this.searchId
        })
        .then(res => {
          let result = res.data
          this.adminData = result.data.list
          this.totalSize = result.data.total
          let clientHieght = document.body.clientHeight
          this.tabMaxHeight = clientHieght - 60 - 30 - 30 - 50 - 50
        })
    },
    //  
    addAdmin() {
      this.addAdminData = {
        username: '',
        password: '',
        realname: '',
        email: '',
        telephone: '',
        isEnable: false
      }
      this.addAdminDialogVisible = true
    },
    // 
    submitAddAdmin(formName) {
      this.addAdminDialogVisible = false
      adminService.addAdminData(this.adminPath.addPath, this.addAdminData).then(res => {
        this.getAdminData()
      })
    },
    // 
    delAdminItem(row) {
      this.delAdminId = row.id
      this.delAdminDialogVisible = true
    },
    // 
    submitDelAdmin() {
      this.delAdminDialogVisible = false
      adminService
        .delAdminData(this.adminPath.delPath, {
          id: this.delAdminId
        })
        .then(res => {
          this.getAdminData()
        })
    },
    // 
    editAdminItem(row) {
      this.editAdminData.id = row.id
      this.editAdminData.password = row.password
      this.editAdminData.realname = row.realname
      this.editAdminData.email = row.email
      this.editAdminData.telephone = row.telephone
      this.editAdminData.isEnable = row.isEnable
      this.editAdminDialogVisible = true
    },
    // 
    submitEditAdmin() {
      adminService.updateAdminData(this.adminPath.editPath, this.editAdminData).then(res => {
        this.getAdminData()
      })
      this.editAdminDialogVisible = false
    },
    // 
    addAdminPermission(row) {
      this.currentAdminId = row.id
      this.editAdminPermissionDialogVisible = true
      // 
      permissionService.getPermissionData(this.permissionPath.getPath, {}).then(res => {
        this.permissionData = res.data.data.list
      })
      //
      adminService
        .searchAdminPermission(this.adminPath.searchPermissionPath, {
          userId: this.currentAdminId
        })
        .then(res => {
          this.adminPermissionData = res.data.data
        })
    },
    // 
    submitEditAdminPermission() {
      this.editAdminPermissionDialogVisible = false
      adminService
        .addAdminPermission(this.adminPath.addPermissionPath, {
          userId: this.currentAdminId,
          authorityIds: this.adminPermissionData
        })
        .then(res => {
          this.getAdminData()
        })
    },
    // 
    addAdminRole(row) {
      this.currentAdminId = row.id
      this.editAdminRoleDialogVisible = true
      // 
      roleService.getRoleData(this.rolePath.getPath, {}).then(res => {
        this.roleData = res.data.data.list
      })
      // 
      adminService
        .searchAdminRole(this.adminPath.searchRolePath, {
          userId: this.currentAdminId
        })
        .then(res => {
          this.adminRoleData = res.data.data
        })
    },
    //  
    submitEditAdminRole() {
      this.editAdminRoleDialogVisible = false
      adminService
        .addAdminRole(this.adminPath.addRolePath, {
          userId: this.currentAdminId,
          roleIds: this.adminRoleData
        })
        .then(res => {
          this.getAdminData()
        })
    },
    // 
    addAdminMenu(row) {
      this.currentAdminId = row.id
      this.editAdminMenuDialogVisible = true
      // 
      menuService.getMenuList(this.menuPath.getPath, {}).then(res => {
        this.menuList = res.data.data
      })
      // 
      adminService
        .searchAdminMenu(this.adminPath.searchMenuPath, {
          userId: this.currentAdminId
        })
        .then(res => {
          this.adminMenuData = res.data.data
        })
    },
    // 
    submitEditAdminMenu() {
      this.editAdminMenuDialogVisible = false
      adminService
        .addAdminMenu(this.adminPath.addMenuPath, {
          userId: this.currentAdminId,
          menuIds: this.$refs.tree.getCheckedKeys()
        })
        .then(res => {
          this.getAdminData()
        })
    },
    // 
    handleClose() {
      this.addAdminDialogVisible = false
      this.delAdminDialogVisible = false
      this.editAdminDialogVisible = false
      this.editAdminPermissionDialogVisible = false
      this.editAdminRoleDialogVisible = false
      this.editAdminMenuDialogVisible = false
    },
    // ----------------------------------------------------
    handleSizeChange(val) {
      this.pageSize = val
      this.getAdminData()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getAdminData()
    }
  }
}
</script>
<style>
.opetion {
  overflow: hidden;
}
</style>




